// tslint:disable:no-unused-variable member-ordering
// noinspection ES6UnusedImports

import {serialize, deserialize, deserializeArray, plainToClass, classToPlain} from 'class-transformer';
import {Inject, Injectable, Optional, OpaqueToken} from '@angular/core';
import {
  Http,
  Headers,
  URLSearchParams,
  RequestMethod,
  RequestOptions,
  RequestOptionsArgs,
  Response,
  ResponseContentType
} from '@angular/http';
import {Observable} from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import * as models from '../model/models';
import {BASE_PATH} from '../variables';
import {Configuration} from '../configuration';
import {ClassType} from '../classType';


export const IAbacApi = new OpaqueToken('IAbacApi');

export interface IAbacApi {


    /**
     * Export Suppliers and Beneficiaries information.
     * 
     * @param c 
     */
    exportAbacInformation<T extends models.ResponseDTO>(c?: ClassType<T>): Observable<T>;
    /**
     * Returns information about the state of the applications for a given Publication
     * 
     * @param c 
     * @param publicationId 
     */
    getPublicationAppliersInfo<T extends models.ResponseDTO>(publicationId: number, c?: ClassType<T>): Observable<T>;
    /**
     * Imports ABAC information
     * 
     * @param c 
     * @param body 
     */
    importAbacInformation<T extends models.ResponseDTO>(body?: string, c?: ClassType<T>): Observable<T>;

}

@Injectable()
export class AbacApi implements IAbacApi {
    protected basePath = 'http://localhost:8080/wifi4eu/api';
    public defaultHeaders: Headers = new Headers();
    public configuration: Configuration = new Configuration();

    constructor(protected http: Http,
                @Optional() @Inject(BASE_PATH) basePath: string,
                @Optional() configuration: Configuration) {
        if (basePath) {
            this.basePath = basePath;
        }
        if (configuration) {
            this.configuration = configuration;
        }
    }



    /**
     * Export Suppliers and Beneficiaries information.
     * 
     * @param c
     */
    exportAbacInformation<T extends models.ResponseDTO>(c?: ClassType<T>): Observable<T> {
        // noinspection TypeScriptValidateTypes
        return this.exportAbacInformationWithHttpInfo()
                .map((response: Response) => {
                    if (response.status === 204) {
                        return undefined;
                    } else if (c) {
                        return deserialize(c, response.text());
                    } else {
                        return response.json();
                    }
                });
        }




    /**
     * Returns information about the state of the applications for a given Publication
     * 
     * @param c
     * @param publicationId 
     */
    getPublicationAppliersInfo<T extends models.ResponseDTO>(publicationId: number, c?: ClassType<T>): Observable<T> {
        // noinspection TypeScriptValidateTypes
        return this.getPublicationAppliersInfoWithHttpInfo(publicationId)
                .map((response: Response) => {
                    if (response.status === 204) {
                        return undefined;
                    } else if (c) {
                        return deserialize(c, response.text());
                    } else {
                        return response.json();
                    }
                });
        }




    /**
     * Imports ABAC information
     * 
     * @param c
     * @param body 
     */
    importAbacInformation<T extends models.ResponseDTO>(body?: string, c?: ClassType<T>): Observable<T> {
        // noinspection TypeScriptValidateTypes
        return this.importAbacInformationWithHttpInfo(body)
                .map((response: Response) => {
                    if (response.status === 204) {
                        return undefined;
                    } else if (c) {
                        return deserialize(c, response.text());
                    } else {
                        return response.json();
                    }
                });
        }



    /**
     * Export Suppliers and Beneficiaries information.
     * 
     */
    private exportAbacInformationWithHttpInfo(): Observable<Response> {
        const path = this.basePath + `/abac/exportAbacInformation`;


        let queryParameters = new URLSearchParams();
        let headers = new Headers(this.defaultHeaders.toJSON()); // https://github.com/angular/angular/issues/6845







        let requestOptions: RequestOptionsArgs = new RequestOptions({
            method: RequestMethod.Get,
            headers: headers,
            search: queryParameters,
            responseType: ResponseContentType.Json
        });

        return this.http.request(path, requestOptions);
    }

    /**
     * Returns information about the state of the applications for a given Publication
     * 
     * @param publicationId 
     */
    private getPublicationAppliersInfoWithHttpInfo(publicationId: number ): Observable<Response> {
        const path = this.basePath + `/abac/publications/${publicationId}`;
//        .replace('{' + 'publicationId' + '}', String(publicationId));  
// not needed as long as the Angular2Typescript language generates the path as TypeScript template string 
// (https://basarat.gitbooks.io/typescript/content/docs/template-strings.html)

        let queryParameters = new URLSearchParams();
        let headers = new Headers(this.defaultHeaders.toJSON()); // https://github.com/angular/angular/issues/6845
        // verify required parameter 'publicationId' is not null or undefined
        if (publicationId === null || publicationId === undefined) {
            throw new Error('Required parameter publicationId was null or undefined when calling getPublicationAppliersInfo.');
        }







        let requestOptions: RequestOptionsArgs = new RequestOptions({
            method: RequestMethod.Get,
            headers: headers,
            search: queryParameters,
            responseType: ResponseContentType.Json
        });

        return this.http.request(path, requestOptions);
    }

    /**
     * Imports ABAC information
     * 
     * @param body 
     */
    private importAbacInformationWithHttpInfo(body?: string ): Observable<Response> {
        const path = this.basePath + `/abac/importAbacInformation`;


        let queryParameters = new URLSearchParams();
        let headers = new Headers(this.defaultHeaders.toJSON()); // https://github.com/angular/angular/issues/6845





        headers.set('Content-Type', 'application/json');


        let requestOptions: RequestOptionsArgs = new RequestOptions({
            method: RequestMethod.Post,
            headers: headers,
            body: body == null ? '' : /*JSON.stringify*/classToPlain(body), // https://github.com/angular/angular/issues/10612
            search: queryParameters,
            responseType: ResponseContentType.Json
        });

        return this.http.request(path, requestOptions);
    }

}



